



// import "intro.js/introjs.css";
import introJs from "intro.js";
import "intro.js/minified/introjs.min.css";

import {
  DoryContextName,
  IDory,
} from "../../../../../common/shrimp/interfaces/RenderingInterfaces/IDory";
import { ContextManager } from "../../../../../common/shrimp/context";
import Vue from "vue";
export default Vue.extend({ 
  name: "QTutorial",
  data() {
    return {
      currentStepValue: undefined,
    };
  },
  props: {
    _renderingProps: {
      default: {
        context: (undefined as unknown) as () => ContextManager,
        isEditMode:(undefined as unknown) as boolean,
      },
    },
    steps: {
      default: [{ text: "", compID: "" }],
    },
    nextButtonLabel: {
      type: String,
      default: "'Next'",
    },
    skipButtonLabel: {
      type: String,
      default: "'Skip'",
    },
    prevButtonLabel: {
      type: String,
      default: "'Back'",
    },
    doneButtonLabel: {
      type: String,
      default: "Done",
    },
    hidePrevButton: {
      type: Boolean,
    },
    showStepNumbers: {
      type: Boolean,
    },
    keyboardNavigation: {
      type: Boolean,
    },
  },
  mounted() {
    
  },
  destroyed() {
      this.$el?.remove();
   },
  methods: {
    start() {
     
      if(this._renderingProps.isEditMode==false){
      this.manupilateDom();
      const introJS = require("intro.js");
      var vm = this;
      introJS
        .introJs()
        .setOptions({
          nextLabel: this.nextButtonLabel,
          prevLabel: this.prevButtonLabel,
          skipLabel: this.skipButtonLabel,
          doneLabel: this.doneButtonLabel,
          hidePrev: this.hidePrevButton,
          showStepNumbers: this.showStepNumbers,
          keyboardNavigation: this.keyboardNavigation,
        })
        .onbeforeexit(function (this) {
          var currentStepIndex = this._currentStep;
          var currentStep = currentStepIndex + 1;
          vm.currentStepValue = currentStep;
          vm.$emit("beforeExit");
        })
        .onafterchange(function (this) {
          vm.$emit("onafterchange");
        })
        .oncomplete(function (this) {
          vm.$emit("oncomplete");
        })
        .start();
      }
    },
    goToStep(value) {
      this.manupilateDom();
      const introJS = require("intro.js");
      introJS.introJs().goToStepNumber(value).start();
    },
    // onbeforeexit() {
    //   this.manupilateDom();
    //   var vm =this
    //   introJs()
    //     .start()
    //     .onbeforeexit(function (this) {

    //       var currentStepIndex = this._currentStep;
    //       var currentStep = currentStepIndex + 1;
    //       vm.currentStepValue=currentStep
    //        vm.$emit("beforeExit");
    //     });
    // },

    // onafterchange() {
    //   this.manupilateDom();
    //   var vm = this;
    //   introJs()
    //     .start()
    //     .onafterchange(function (targetElement) {
    //       vm.$emit("onafterchange");
    //     });
    // },

    // oncomplete() {
    //   this.manupilateDom();
    //   var vm = this;
    //   introJs()
    //     .start()
    //     .oncomplete(function () {
    //       vm.$emit("oncomplete");
    //     });
    // },

    exit() {
      this.manupilateDom();
      const introJS = require("intro.js");

      introJs().exit();
    },
    manupilateDom() {
      this.steps.forEach((step, index) => {
        const compColl = this._renderingProps.context()?.GetItemSingle<IDory>(DoryContextName)?.GetComponentCollectionByID(step.compID);
        index = index + 1;
        const compDom = compColl?.domElement;
        if (!compDom) {
          return;
        }
        compDom.setAttribute("data-step", index.toString());
        compDom.setAttribute("data-intro", step.text);
      });
    },
  },
});
